import React, { useState } from "react";
import mainData from "../../content/Main.yml";
import { Link } from "gatsby";
import Particles from "../Particles";
import Typewriter from 'typewriter-effect';

const HomeSection = () => {
  const [linkVisible, setLinkVisible] = useState(false);
  const data = mainData.home;

  return (
    <article data-sal="slide-up" data-sal-delay="300" data-sal-easing="ease">
      {/* <Particles /> */}
      
      <h4>{data.greeting} {data.nameIntro}</h4>
      <h1>{data.name}</h1>
      <h5>{data.description}</h5>
      <Link className="nav-link" to="/expertise">{data.next}</Link>

      {/* <Typewriter 
        options={{
          delay: 50,
          cursorClassName: "cursor"
        }}
        onInit={(typewriter) => {
          typewriter
            .pauseFor(500)
            .typeString(`<h4>${data.greeting}</h4>`)
            .pauseFor(1000)
            .typeString(`<h4>${data.nameIntro}</h4>`)
            .pauseFor(500)
            .typeString(`<h1>${data.name}</h1>`)
            .pauseFor(500)
            .typeString(`<h5>${data.description}</h5>`)
            .pauseFor(500)
            .callFunction(() => {
              setLinkVisible(true);
            })
            .start();
        }}
      />
      { linkVisible &&
        <Link className="nav-link reveal-animation" to="/about">{data.next}</Link>
      } */}
    </article>
  );
};

export default HomeSection;
